import React, {useEffect, useRef, useState} from 'react';
import './App.css';

function App() {

  const inputRef  = useRef(null);
  const searchRef = useRef(null);
  const [title, setTitle] = useState('');

  useEffect(() => {
    inputRef.current.focus();
  }, []);

  function searchKeyDown(e){
    if(e.key === "Enter"){
      searchRef.current.focus();
    }
  }

  function submitKeyDown(){
    alert('Submitted');
  }

  const handleChange = e => {
    let x = setTitle(e.target.value);
    console.log(x);
    // setTitle(e.target.value);
    // console.log(e.target.value);
    // console.log(e.target.name);
  }


  return (
    <div className="App">
      <header className="App-header">
        <h1>Search your book</h1>
        <input type="text" onKeyDown={searchKeyDown}  onChange={handleChange} ref={inputRef} id="name" name="name" required placeholder="Search a book..." minLength="1" maxLength="40" size="30" />
      </header>

      <button onKeyDown={submitKeyDown} ref={searchRef} id="search">Search</button>
      
      <div id="result"></div>

    </div>
  )
}

export default App;
